.page-title {
  padding: 0.5em 1.5em;
}
.flex-1 {
  flex: 1 1;
}
.floating-edit-button-wrapper {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #00b0f0;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.12);
  position: absolute;
  top: 10%;
  right: 5%;
  border: none;
  display: flex;
}
.floating-edit-button-wrapper.\--profile-picture {
  top: 80%;
  left: 70%;
}
.floating-edit-button-wrapper svg {
  margin: auto;
}
.floating-edit-button-wrapper:hover {
  box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.12);
  cursor: pointer;
}
.friends-list-container {
  border-radius: 8px;
  background: #ffffff;
  flex: 1 1;
  align-self: flex-start;
}
.friends-list-header {
  padding: 1em 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.friends-list-header h5,
.friends-list-header p {
  margin-bottom: 0.2em;
}
.friends-search-container {
  padding: 0.5em;
  background: #f1f1f1;
}

.friends-search-container .searchContainer {
  display: inline-flex;
  border: 1px solid #c4c4c4;
  border-radius: 4px;
  overflow: hidden;
  max-height: 40px;
  width: 90%;
  align-items: center;
}
.friends-search-container.chatbox .searchContainer {
  margin: 0.6em 1em;
}
.searchIconContainer {
  background: #f1f1f1;
  margin: 0;
  height: 100%;
}
.searchIcon {
  padding: 0.8rem;
  font-size: 16px;
}

.searchBox {
  border: 0;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  flex: 1 1;
}
.friends-list {
  padding: 1em;
}
.notifications-container {
  background: #ffffff;
  border: 1px solid #f1f1f1;
  border-radius: 4px;
  width: 100%;
}
.notifications-page-header {
  padding: 0.5em 1em;
  border-bottom: 1px solid #f1f1f1;
}
.single-notification {
  display: flex;
  padding: 1em 2em;
  justify-content: space-between;
  border-bottom: 1px solid #f1f1f1;
}
.notification-owner-wrapper {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 15px;
}
.notification-owner {
  width: 100%;
  height: 100%;
}
.single-notification-link, 
.single-notification-link:hover, 
.single-notification-link:visited, 
.single-notification-link:active {
  color: inherit !important;
  display: flex;
  text-decoration: none;
}
.chat-list {
  background: #fff;
  overflow: auto;
  height: calc(100vh - 255px);
}
.chat-head {
  padding: 16px;
  border: 1px solid #F0F0F0;
  display: flex;
}
.chat-head-photo {
  height: 40px;
  width: 40px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.chat-head-photo > img {
  width: 100%;
  height: 100%;
}
.chat-head-content {
  width: calc(100% - 50px);
}
.chat-head-user-and-time {
  display: flex;
  justify-content: space-between;
}
.chat-head p {
  margin-bottom: 2px;
}
.previous-chat {
  overflow: hidden;
  width: 80%;
  text-overflow: ellipsis;  
  white-space: nowrap;
}
.chat-head-active, .chat-head:hover {
  background: #E8F6FB;
  cursor: pointer;
}
.chat-head a {
  text-decoration: none;
}
.message-count {
  color: #fff;
  background: #09526D;
  border-radius: 2px;
  font-size: 12px !important;
  padding-left: 4px;
  padding-right: 4px;
  height: 18px;
  min-width: 18px;
  text-align: center;
}
.chat-header {
  display: flex;
  padding: 16px;
  border: 1px solid #F0F0F0;
}
.main-chat-container {
  background: #fff;
  height: calc(100vh - 120px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.close-chat {
  background: transparent;
  border: none;
  font-weight: 800;
  cursor: pointer;
}
.chat-interface {
  padding: 16px;
  flex: 1 1;
  overflow: auto;
  flex-direction: column;
  padding-bottom: 0;
}
.chat-message-wrapper {
  display: flex;
  padding-bottom: 16px;
}
.chat-message-wrapper.owner {
  justify-content: flex-end;
} 
.message-owner {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  align-self: flex-end;
  margin-right: 12px;
  margin-bottom: 8px;
}
.message-owner img {
  width: 100%;
  height: 100%;
}
.message-wrapper {
  max-width: 300px;
}
.message-wrapper p {
  margin-bottom: 0;
}
.other-chat-message {
  background:  #E1E1E1;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner-chat-message {
  background:  #DBEDF4;;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner .message-owner {
  margin-left: 12px;
  margin-right: 0;
}
.message-time {
  text-align: right;
}
.chat-input-wrapper {
  padding: 1em;
  background: #fafafa;
  margin: 0 -16px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}
.chat-input-wrapper .form-control {
  flex: 1 1;
}
.chat-input-wrapper .btn {
  margin: 0;
  padding: 0.5em 1em;
  margin-left: 1em;
}
.countdown {
  border-radius: 8px;
}
.countdown>h3, .countdown>p {
  font-weight: 900;
}
.tabs-header {
  background: #ffffff;
  display: flex;
  justify-content: space-between;
}

.filter-tabs .btn-tabs {
  background: transparent;
  color: #333333;
  margin: 0;
  padding: 1em;
  border: none !important;
}
.filter-tabs .btn-tabs.active,
.filter-tabs .btn-tabs:hover {
  color: #00b0f0;
}
.btn-tabs:focus,
.btn-tabs:active,
.btn-tabs:visited {
  border: none;
  box-shadow: none !important;
}
.filter-button {
  background: transparent;
  border: none;
  padding: 1em;
}
.filter-button:hover {
  cursor: pointer;
}
.filter-dropdown {
  background: #ffffff;
  border-radius: 4px;
  position: absolute;
  right: 0;
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.25);
  min-width: 200px;
  display: none;
  margin-top: -10px;
}
.filter-dropdown.show {
  display: block;
}
.filter-dropdown-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.filter-dropdown-button {
  width: 100%;
  background: transparent;
  border: none;
  text-align: left;
  padding: 0.6em 1em;
}
.filter-dropdown-button:hover {
  color: #ffffff;
  background: #00b0f0;
  cursor: pointer;
}
.poll-form .form-check {
  display: flex;
}
.poll-form .form-check-i {
  opacity: 0;
  cursor: pointer;
  height: 18px;
  width: 18px;
  position: absolute;
}
.checkmark {
  top: 0;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #333333;
}
.poll-form label {
  margin-left: 1em;
}
.container input:checked ~ .checkmark {
  background-color: #00b0f0;
}
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.container input:checked ~ .checkmark:after {
  display: block;
}

.suggestions {
  border-radius: 8px;
  background: #ffffff;
  margin-bottom: 30px;
}
.suggestions-header {
  background: #f2f2f2;
  padding: 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.suggestion-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  overflow: hidden;
}
.suggestion-image {
  width: 100%;
  height: 100%;
}
.single-suggestion {
  display: flex;
  align-content: center;
  padding: 1em;
  justify-content: space-between;
}
.single-suggestion .btn {
  font-size: 12px;
  padding: 0.7em;
}
.suggested-name {
  margin: auto 0;
}
.tagline {
  padding: 1em;
  padding-bottom: 0;
  margin-bottom: 0.3em;
}
.dashed-border-bottom {
  border-bottom: 1px dashed #c4c4c4;
}
.suggestion-footer {
  padding: 1em 1em;
  padding-top: 0.1em;
}
.suggestion-footer-link {
  color: #00b0f0;
  font-size: 16px;
}
.suggestion-footer-link:hover {
  cursor: pointer;
  text-decoration: none;
  color: #333333;
}

.trending-section-header {
  padding: 1em;
}
.trending-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 1em;
  overflow: hidden;
}
.trending-image {
  width: 100%;
  height: 100%;
}
.trending-name p {
  margin-bottom: 0;
  font-size: 16px;
}
.trending-name {
  margin: auto 0;
}

.trending-section-content {
  padding: 1em;
  border-radius: 8px;
  background: #ffffff;
}

.trending-text p {
  font-size: 14px;
  text-align: justify;
  margin-top: 0.5em;
}
.like-btn {
  background: transparent;
  border: none;
  padding: 0;
}
.like-btn:hover {
  cursor: pointer;
  color: #00b0f0;
}
.trending-post {
  padding: 1em 0;
}

.sidebar {
  width: 270px;
  margin-right: 1em;
}
.sidebar p {
  font-size: 14px;
}
.sidebar a,
.sidebar-section a,
.sidebar-section a:hover {
  color: #333;
  text-decoration: none !important;
}
.sidebar-top {
  background: #ffffff;
  border-radius: 4px;
  padding-bottom: 2em;
  margin-bottom: 1em;
}
.sidebar-footer {
  background: #ffffff;
  border-radius: 4px;
  padding: 1em;
}
.sidebar-cover-image-wrapper {
  width: 100%;
  height: 130px;
  border-radius: 4px;
  overflow: hidden;
}
.sidebar-cover-image {
  width: 100%;
  min-height: 200px;
  height: auto;
}
.sidebar-section {
  margin: 0 1em;
  border-bottom: 1px dashed #c4c4c4;
  padding: 1em;
  border-top: 0px solid white;
}
.sidebar-section.\--lift-up {
  margin-top: -60px;
}
.sidebar-section.\--no-border {
  border: none;
}
.sidebar-profile-image-wrapper {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin: auto;
  margin-bottom: 1em;
  overflow: hidden;
}
.sidebar-profile-image,
.politician-profile-image {
  width: 100%;
  height: 100%;
}
.sidebar-section p {
  margin-bottom: 0.2em;
}
.sidebar-section .text-gclout-blue {
  margin-bottom: 0.5em;
}
.section-half {
  width: 50%;
}

.politician-profile-image-wrapper {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: auto;
  overflow: hidden;
}
.sidebar-politician {
  margin: 1em 0;
}
.sidebar-footer > p > a {
  color: #333333;
}
.sidebar-footer > p > a:hover {
  color: #00b0f0;
  text-decoration: none;
}
.sidebar-footer .smaller-margin {
  margin-bottom: 0.2em;
}
.advert-section {
  padding-top: 1em;
}
.advert-section p {
  font-size: 16px;
}
.advert-link {
  color: #333333;
}
.advert-link:hover {
  color: #00b0f0;
  text-decoration: none;
}
.friends-details {
  padding-top: 0.6em;
}
@media screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }
}

.make-post {
  border-radius: 8px;
  background: #ffffff;
  flex: 1 1;
  align-self: flex-start;
  margin-bottom: 1em;
}
.make-post.not-rounded > .make-post-header,
.make-post.not-rounded {
  border-radius: 0 !important;
}
.make-post-header {
  background: #f2f2f2;
  padding: 0.5em 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.btn-tab {
  border: none;
  margin: 0 !important;
  background: transparent;
  border-right: 1px solid #c4c4c4;
  border-radius: 0;
}
.btn-tab:focus,
.btn-tab:active {
  box-shadow: 0 0 0 0;
  border: none;
  outline: none;
  border-right: 1px solid #c4c4c4;
}
.new-post-container {
  max-height: 0px;
  overflow: hidden;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.new-post-container.show {
  max-height: 1000px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.close-btn {
  font-size: 21px;
  padding-bottom: 0;
  margin-bottom: 0;
}
.close-btn:hover {
  cursor: pointer;
  color: #00b0f0;
}
.dropzone {
  position: relative;
  width: 120px;
  height: 120px;
  border: 2px dashed #00b0f0;
  border-radius: 5px;
  color: #00b0f0;
  padding: 30px 0;
  text-align: center;
}
.dropzone:hover {
  cursor: pointer;
}
.image-preview-wrapper {
  width: 120px;
  height: 120px;
  border-radius: 5px;
  margin: 0 1em;
  overflow: hidden;
}
.image-preview-wrapper > img {
  width: auto;
  height: 100%;
}
.preview-delete {
  background: #f00048;
  color: #ffffff;
  text-align: center;
  padding: 1em;
  border: none;
  border-radius: 4px;
  font-size: 11px;
  margin: auto;
}
.preview-delete:hover {
  cursor: pointer;
}
.preview-overlay {
  background: rgba(0, 0, 0, 0.4);
  width: 120px;
  height: 120px;
  position: absolute;
  z-index: 11;
  display: none;
  align-content: center;
  justify-content: center;
  margin-top: -120px;
  border-radius: 5px;
}
.image-preview-wrapper:hover > .preview-overlay {
  display: flex;
}
.button-dropdown {
  display: none;
  left: -100px;
  right: 0;
  z-index: 9999999;
  background: #ffffff;
  color: #333333;
  border-radius: 4px;
  box-shadow: 1px 2px 1px 0px rgba(0,0,0,0.3);
}
.button-dropdown.show {
  display: block;
}
.button-dropdown-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.button-dropdown-list-button {
  background: transparent;
  padding: 0.25em 7em 0.25em 0;
  margin: 0;
  text-align: left;
  border: none;
}
.button-dropdown-list-button:hover {
  background-color: #00b0f0;
  color: #ffffff;
  text-align: left;
}
/*[data-placement] {
  left: -175px !important;
}*/
.btn-white {
  margin: 0;
  background: #ffffff;
}

.post-link,
.post-link:hover,
.post-link:focus,
.post-link:visited,
.post-link:active {
  color: #333333;
  text-decoration: none;
}
.post-container {
  background: #ffffff;
  padding: 1em 2em;
}
.post-container:hover {
  background: #fdfdfd;
}
.post-owner {
  display: flex;
  align-content: center;
}
.post-owner-image-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 100px;
  overflow: hidden;
  margin-right: 1em;
}
.post-owner-image {
  width: 100%;
  height: 100%;
}
.post-owner-details > p {
  font-weight: 600;
  margin: auto 0;
}
.post-owner-details {
  align-self: center;
}
.post-type {
  opacity: 0.7;
  font-weight: 400 !important;
}
.post-content {
  padding: 1em 0 0 0;
}
.post-content > p {
  text-align: justify;
}
.post-actions-container {
  padding: 1em 2em;
  background: #f1f1f1;
  display: flex;
  justify-content: space-between;
}
.post-action {
  background: transparent;
  border: none;
  line-height: 24px;
  font-size: 19px;
}
.flipped-reply {
  -webkit-transform: matrix(-1, 0, 0, 1, 0, 0);
          transform: matrix(-1, 0, 0, 1, 0, 0);
}
.post-action:hover, .post-action.active {
  cursor: pointer;
  color: #00b0f0;
}
.post-media-container {
  width: 100%;
}
.post-image {
  width: 100% !important;
}
.petition-header {
  background: #ffffff;
  border-bottom: 1px solid #cfcfcf;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 1em;
}
.petition-header p , .petition-header a{
  margin-bottom: 0;
  padding-bottom: 0;
  text-decoration: none;
  color: #333333;
}
.styled-text {
  margin-bottom: 0;
  padding: 0;
  align-self: center;
}
.post-actions-container .btn {
  margin: 0;
}
.dropup {
  padding: 1em 3em;
  border-radius: 8px;
  display: none;
  left: -10em;
  margin-top: -10px
}
.dropup.show {
  display: block;
}
.dropup-list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-content: center;
}
.dropup-list-item button {
  border: none;
  margin: 4px;
  background: #00b0f0;
  border-radius: 50px;
  padding: 0.5em;
  box-shadow: 2px 2px 8px rgba(0,0 ,0,0.2);
}
.dropup-list-item button:hover {
  box-shadow: 4px 4px 8px rgba(0,0 ,0,0.2);
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
}
.dropup-list-item a{
  padding: 0.9em;
  font-size: 14px;
  color: #ffffff;
  border-radius: 50%;
}
.comment-input-wrapper {
  background: #ffffff;
  padding: 1em 2em;
  display: flex;
  align-content: center;
}
.comment-input-wrapper.hidden {
  display: none !important;
}
.comment-owner-wrapper {
  width: 40px !important;
  height: 40px !important;
  overflow: hidden;
  border-radius: 30px;
}
.comment-owner {
  width: 100%;
  height: 100%;
}
.comment-input-wrapper .form-control {
  margin-left: 1em;
  flex: 1 1;
}

